// 
window.URL = window.URL || window.webkitURL
navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia
let HZRecorder = function (stream, config) {
    config = config || {}
    config.sampleBits = config.sampleBits || 8 //  8, 16
    config.sampleRate = config.sampleRate || (44100 / 6) // (1/6 44100)
    let context = new (window.webkitAudioContext || window.AudioContext)()
    let audioInput = context.createMediaStreamSource(stream)
    let createScript = context.createScriptProcessor || context.createJavaScriptNode
    this.source = context.createBufferSource();
    let recorder = createScript.apply(context, [4096, 1, 1])
    let audioData = {
        size: 0, // 
        buffer: [], // 
        inputSampleRate: context.sampleRate, // 
        inputSampleBits: 16, //  8, 16
        outputSampleRate: config.sampleRate, // 
        oututSampleBits: config.sampleBits, //  8, 16
        input: function (data) {
            this.buffer.push(new Float32Array(data))
            this.size += data.length
        },
        compress: function () { // 
            // 
            let data = new Float32Array(this.size)
            let offset = 0
            for (let i = 0; i < this.buffer.length; i++) {
                data.set(this.buffer[i], offset)
                offset += this.buffer[i].length
            }
            // 
            let compression = parseInt(this.inputSampleRate / this.outputSampleRate)
            let length = data.length / compression
            let result = new Float32Array(length)
            let index = 0; let j = 0
            while (index < length) {
                result[index] = data[j]
                j += compression
                index++
            }
            return result
        },
        encodeWAV: function () {
            let sampleRate = Math.min(this.inputSampleRate, this.outputSampleRate)
            let sampleBits = Math.min(this.inputSampleBits, this.oututSampleBits)
            let bytes = this.compress()
            let dataLength = bytes.length * (sampleBits / 8)
            let buffer = new ArrayBuffer(44 + dataLength)
            let data = new DataView(buffer)
            let channelCount = 1// 
            let offset = 0
            let writeString = function (str) {
                for (let i = 0; i < str.length; i++) {
                    data.setUint8(offset + i, str.charCodeAt(i))
                }
            }
            // 
            writeString('RIFF'); offset += 4
            // ,-8
            data.setUint32(offset, 36 + dataLength, true); offset += 4
            // WAV
            writeString('WAVE'); offset += 4
            // 
            writeString('fmt '); offset += 4
            // , 0x10 = 16
            data.setUint32(offset, 16, true); offset += 4
            //  (PCM)
            data.setUint16(offset, 1, true); offset += 2
            // 
            data.setUint16(offset, channelCount, true); offset += 2
            // ,,
            data.setUint32(offset, sampleRate, true); offset += 4
            //  () /8
            data.setUint32(offset, channelCount * sampleRate * (sampleBits / 8), true); offset += 4
            //   /8
            data.setUint16(offset, channelCount * (sampleBits / 8), true); offset += 2
            // 
            data.setUint16(offset, sampleBits, true); offset += 2
            // 
            writeString('data'); offset += 4
            // ,-44
            data.setUint32(offset, dataLength, true); offset += 4
            // 
            if (sampleBits === 8) {
                for (let i = 0; i < bytes.length; i++ , offset++) {
                    let s = Math.max(-1, Math.min(1, bytes[i]))
                    let val = s < 0 ? s * 0x8000 : s * 0x7FFF
                    val = parseInt(255 / (65535 / (val + 32768)))
                    data.setInt8(offset, val, true)
                }
            } else {
                for (let i = 0; i < bytes.length; i++ , offset += 2) {
                    let s = Math.max(-1, Math.min(1, bytes[i]))
                    data.setInt16(offset, s < 0 ? s * 0x8000 : s * 0x7FFF, true)
                }
            }
            return new Blob([data], { type: 'audio/mp3' })
        }
    }
    // 
    this.start = function () {
        audioInput.connect(recorder)
        recorder.connect(context.destination)
    }
    // 
    this.stop = function () {
        recorder.disconnect()
    }
    // 
    this.getBlob = function () {
        this.stop()
        return audioData.encodeWAV()
    }
    // 
    this.play = function (audio) {
        let downRec = document.getElementById('downloadRec')
        downRec.href = window.URL.createObjectURL(this.getBlob())
        downRec.download = new Date().toLocaleString() + '.mp3'
        audio.src = window.URL.createObjectURL(this.getBlob())
    }

    // 
    this.upload = function (url, callback) {
        let fd = new FormData()
        fd.append('audioData', this.getBlob())
        let xhr = new XMLHttpRequest()
        /* eslint-disable */
        if (callback) {
            xhr.upload.addEventListener('progress', function (e) {
                callback('uploading', e)
            }, false)
            xhr.addEventListener('load', function (e) {
                callback('ok', e)
            }, false)
            xhr.addEventListener('error', function (e) {
                callback('error', e)
            }, false)
            xhr.addEventListener('abort', function (e) {
                callback('cancel', e)
            }, false)
        }
        /* eslint-disable */
        xhr.open('POST', url)
        xhr.send(fd)
    }
    // 
    HZRecorder.recorder = recorder;
    // recorder.onaudioprocess = function (e) { //
    //     var buffer = e.inputBuffer.getChannelData(0); //PCM32
    //     // buffer
    //     var maxVal = 0;
    //     for (var i = 0; i < buffer.length; i++) {
    //         if (maxVal < buffer[i]) {
    //             maxVal = buffer[i];
    //         }
    //     }
    //     // 
    //     // test.vue
    //     // console.log(maxVal * 10000)
    // };

}
// 
HZRecorder.throwError = function (message) {
    alert(message)
    throw new function () { this.toString = function () { return message } }()
}
// 
HZRecorder.canRecording = (navigator.getUserMedia != null)
// 
HZRecorder.get = function (callback, config) {
    if (callback) {
        if (navigator.getUserMedia) {
            navigator.getUserMedia(
                { audio: true } // 
                , function (stream) {
                    let rec = new HZRecorder(stream, config);
                    callback(rec)
                }
                , function (error) {
                    switch (error.code || error.name) {
                        case 'PERMISSION_DENIED':
                        case 'PermissionDeniedError':
                            HZRecorder.throwError('')
                            break
                        case 'NOT_SUPPORTED_ERROR':
                        case 'NotSupportedError':
                            HZRecorder.throwError('')
                            break
                        case 'MANDATORY_UNSATISFIED_ERROR':
                        case 'MandatoryUnsatisfiedError':
                            HZRecorder.throwError('')
                            break
                        default:
                            HZRecorder.throwError(':' + (error.code || error.name))
                            break
                    }
                })
        } else {
            HZRecorder.throwErr(''); return
        }
    }
}

export default  HZRecorder

